.todo-app{  
  /* background-color: red; */
}
.todo-app-content{
  background-color: purple;
  width: 400px;
  margin: 0 auto;
  padding: 10px;
  height: 100vh;
}

.title{
  text-align: center;
  color: white;
}
.todo-form{
  /* background-color: aqua; */
}
/* width */
::-webkit-scrollbar {
  width: 8px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 10px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: rgb(104, 104, 104);
  border-radius: 10px;
}

.container{
  height: 75vh;
  overflow-y: scroll;
}
input{
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 10px;
  padding-right: 10px;
  border-radius: 8px;
  border: 0;
  margin: 4px 4px;
  width: 63%;
}
button{
  padding-top: 10px;
  padding-bottom: 10px;
  padding-left: 20px;
  padding-right: 20px;
  border-radius: 8px;
  border: 0;
  margin: 4px 4px;
}
.btn-submit{
  background-color: #22c55e;
  color: white;
  transition: all;
  transition-duration: 300ms;
}
.btn-submit:hover{
  background-color: #16a34a;
}

.todo-row{
  padding: 4px;
}
.todo-text{
  margin-top: 8px;
  color: white;
  font-weight:500;
  font-size: 20px;
  /* background-color: #16a34a; */
  width: 55%;
  float: left;
  word-wrap: break-word;
}
.icons{
  /* background-color: #22c55e; */
  /* float: left; */
  /* width: 40%; */
}
.delete-icon{
  background-color: #c52222;
  color: white;
  transition: all;
  transition-duration: 300ms;
}
.delete-icon:hover{
  background-color: #a31616;
}
.edit-icon{
  background-color: #226ec5;
  color: white;
  transition: all;
  transition-duration: 300ms;
}
.edit-icon:hover{
  background-color: #164ea3;
}